
import { mapMutations } from 'vuex'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapState, mapActions, mapGetters } from 'vuex'

export default {

components: {
Swipeout,
SwipeoutItem,
SwipeoutButton,
Cell
},

computed: {
...mapGetters([
"selectedCities",
]),
},

mounted() {
this.SET_HEADER({title:this.$t('Cities')})
},

methods: {
...mapMutations(['SET_HEADER']),
...mapActions([
'changeMapCity',
'changeWeatherCity',
'deleteCity',
]),
onChange(name){
var fromPath = this.$store.state.route.from.fullPath
if(fromPath === '/'){
this.changeMapCity(name)
}else if(fromPath === '/weather'){
this.changeWeatherCity(name)
}
this.$router.go(-1)


},
},

}




